<template>
  <div class="Login" >
    <div class="header" style="background-image: url('./static/img/city.jpg'); background-size: cover; background-position: top center;">
    <el-row style="height:600px">
        <el-col :span="8"><div class="grid-content"></div></el-col>
        <el-col :span="8"><div class="grid-content">
    <el-container>
    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm" style="margin-top:100px">
  <el-form-item label="账号" prop="id">
    <el-input type="type" v-model="ruleForm2.id" auto-complete="off"></el-input>
  </el-form-item>
  <el-form-item label="密码" prop="pass">
    <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
  </el-form-item>
  <!-- <el-form-item label="验证码" prop="check">
    <el-input v-model.number="ruleForm2.check"></el-input>
  </el-form-item> -->
  <el-form-item label="验证">
  <div class="drag">
    <div class="drag_bg"></div>
    <div class="drag_text">{{confirmWords}}</div>
    <div @mousedown="mousedownFn($event)" class="handler handler_bg"></div>
</div>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm2')" :disabled="!confirmSuccess">登录</el-button>
    <el-button @click="resetForm('ruleForm2')">重置</el-button>
    <el-button :plain="true" @click="open4" v-show="a">错误</el-button>
  </el-form-item>
</el-form>
</el-container>
</div></el-col>
 <el-col :span="8"><div class="grid-content"></div></el-col>
    </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
      var testid = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('账号不能为空'));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('pass');
          }
          callback();
        }
      };
      var testpass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else if (value !== this.ruleForm2.pass) {
          callback();
        } else {
          callback();
        }
      };
    return {
      beginClientX:0,      /*距离屏幕左端距离*/
      mouseMoveStata:false,    /*触发拖动状态  判断*/
      maxwidth:258,       /*拖动最大宽度，依据滑块宽度算出来的*/
      confirmWords:'拖动滑块验证',      /*滑块文字*/
      confirmSuccess:false,              /*验证成功判断*/
      a:false,
     ruleForm2: {
          id: '',
          pass: '',
        },
        rules2: {
          id: [
            { validator: testid, trigger: 'blur' }
          ],
          pass: [
            { validator: testpass, trigger: 'blur' }
          ],
        }
    }
  },
    methods: {
      submitForm(formName) {

        var vm =this;
         var data_tmp = {

          "userid": vm.ruleForm2.id,
          "password": vm.ruleForm2.pass,
          // "checkCode": vm.ruleForm2.check,
        };
         var url = "http://localhost:8081/user";
    //    var url = vm.url + "api/login";

        $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
           "userid": vm.ruleForm2.id,
           "password": vm.ruleForm2.pass,
          },
          success: function(data) {
            //将data json字符串转换为json对象
            var Obj = data;
            // console.log(Obj);

            if (Obj.code == 0) {
              console.log("登录成功")
                window.location.href = "#/home";
           
            } else {
               vm.open4();
              console.log("登录失败");
              vm.resetForm(formName);
            }
          },
          error: function(data) {
            console.log(data);
          }
        });
      
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
        open4() {
        this.$message.error('账号或密码错误');
      },
      mousedownFn:function (e) {
                this.mouseMoveStata = true;
                this.beginClientX = e.clientX;
            },       //按下滑块函数
       successFunction(){
                $(".handler").removeClass('handler_bg').addClass('handler_ok_bg');
                this.confirmWords = '验证通过'
                $(".drag").css({'color': '#fff'});
                $(".handler").css({'left':this.maxwidth});
                $(".drag_bg").css({'width': this.maxwidth});
                $('body').unbind('mousemove');
                $('body').unbind('mouseup');
                this.confirmSuccess = true
            }                //验证成功函数
    },
    mounted(){
            $('body').on('mousemove',(e) => {   //拖动，这里需要用箭头函数，不然this的指向不会是vue对象
                if(this.mouseMoveStata){
                    var width = e.clientX - this.beginClientX;
                    if(width>0 && width<=this.maxwidth){
                        $(".handler").css({'left':width});
                        $(".drag_bg").css({'width': width});
                    }else if(width>this.maxwidth){
                        this.successFunction();
                    }
                }
            });
            $('body').on('mouseup',(e) => {     //鼠标放开
                this.mouseMoveStata = false;
                var width = e.clientX - this.beginClientX;
                if(width<this.maxwidth){
                    $(".handler").css({'left':0});
                    $(".drag_bg").css({'width': 0});
                }
            })
        }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
 .drag{
    position: relative;
    background-color: #e8e8e8;
    width: 300px;
    height: 34px;
    /* margin-left: 30px;
    margin-top: 100px; */
    line-height: 34px;
    text-align: center;
}
.handler{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 40px;
    height: 32px;
    border: 1px solid #ccc;
    cursor: move;
}
.handler_bg{
    background: #fff url("data:image/png;base64,") no-repeat center;
}
.handler_ok_bg{
    background: #fff url("data:image/png;base64,") no-repeat center;
}
.drag_bg{
    background-color: #7ac23c;
    height: 34px;
    width: 0px;
}
.drag_text{
    position: absolute;
    top: 0px;
    width: 300px;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    -o-user-select:none;
    -ms-user-select:none;
}

</style>



