<style scoped>
   a {
    color:#000000;
  }

</style>
<template>
  <div class="newalg">

<el-row>
  <el-col :span="6"><div class="grid-content" @click="changenewshow()" style="text-align:left">
      
          <i class="el-icon-circle-plus" v-show="!newshow">
          </i>
          <i class="el-icon-remove" v-show="newshow">
        </i> <a>新建算法</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
</el-row>
  <div v-show="newshow">
   <el-row>
   <el-row>
     <el-col :span="6">
       算法类型
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="algtype"
        clearable>
    </el-input>
     </el-col>
     <el-col :span="6">
       算法名称
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="algname"
        clearable>
    </el-input>
     </el-col>
   </el-row>
      <el-row>
     <el-col :span="6">
       算法包
     </el-col>
     <el-col :span="6">
       <!-- <el-input
        placeholder="请输入内容"
        v-model="jarname"
        clearable>
    </el-input> -->
     <el-select v-model="jarname" placeholder="请选择" style="width:315.8px">
    <el-option
      v-for="item in jarlist"
      :key="item.value"
      :label="item.label"
      :value="item.value">
      <span style="float: left">{{ item.label }}</span>
      <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
    </el-option>
  </el-select>
     </el-col>
     <el-col :span="6">
       算法的类名
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="algclass"
        clearable>
    </el-input>
     </el-col>
   </el-row>
     <el-row>
     <el-col :span="6">
       输入参数
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="inpara"
        clearable>
    </el-input>
     </el-col>
     <el-col :span="6">
       算法描述
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="algdesc"
        clearable>
    </el-input>
     </el-col>
   </el-row>
         <el-button type="primary" @click="submitalg()">提交</el-button>
         <el-button type="danger" @click="clear()">重置</el-button>
         <el-button type="info" @click="changenewshow()">收起</el-button>
   </el-row>
  </div>
</div>
</template>
<script>

  export default {
    data() {
      return {
       newshow:false,
       algtype:'',
       algname:'',
       jarname:'',
       algclass:'',
       inpara:'',
       algdesc:'',
       jarlist:[],
      }
    },
    computed: {
   
  },
    created: function () {
      var vm = this;
      vm.getjar();

     


    }, 
    methods: {
      changenewshow(){
      var vm = this;
      vm.newshow = !vm.newshow;

       },
       submitalg(){
          var vm = this;
        var url = "http://localhost:8081/alg/addone";
           $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
           "algtype":vm.algtype,
           "algname":vm.algname,
           "jarname":vm.jarname,
           "algclass":vm.algclass,
           "parameter":vm.inpara,
           "algdesc":vm.algdesc,
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            if(data.code==0){
             vm.$notify({
             title: '成功',
             message: '上传成功',
             type: 'success'
             });}
            else{
              vm.$notify.error({
               title: '错误',
              message: data.msg
            });
            }
            vm.clear();
           vm.fresh();
          },
           error: function (data) {
            console.log(data);
          }
        });
       },
    fresh() {
      this.$emit('fresh'); //主动触发upup方法，'hehe'为向父组件传递的数据
    },
    clear(){
      var vm= this;
        vm.algtype='';
        vm.algname='';
        vm.jarname='';
        vm.algclass='';
        vm.inpara='';
        vm.algdesc='';
    },
    getjar(){
        var vm = this;
        vm.jarlist = [];
         var url = "http://localhost:8081/jarfile";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "GET",
          dataType: 'json',
          data: {
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data.data;
            if(data.code==0){
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.jarlist.push({
                  "label":Obj[i].jarname,
                  "value":Obj[i].jarfilename,
                });
                
              }}}
              else{
                console.log(data)
              }
          },
           error: function (data) {
            console.log(data);
          }
        });
    }
    }
  }
</script>



