import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import home from '@/components/home'
import datamanage from '@/components/datamanage'
import algmanage from '@/components/algmanage'
import jobconfig from '@/components/jobconfig'
import jobmanage from '@/components/jobmanage'
import result from '@/components/result'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/datamanage',
      name: 'datamanage',
      component: datamanage
    },
    {
      path: '/algmanage',
      name: 'algmanage',
      component: algmanage
    },
    {
      path: '/jobconfig',
      name: 'jobconfig',
      component: jobconfig
    },
    {
      path: '/jobmanage',
      name: 'jobmanage',
      component: jobmanage
    },
    {
      path: '/result',
      name: 'result',
      component: result
    },
  ]
})



