<style scoped>
   a {
    color:#000000;
  }

</style>
<template>
  <div class="uploadalg">

<el-row>
  <el-col :span="6"><div class="grid-content" @click="changenewshow()" style="text-align:left">
      
          <i class="el-icon-circle-plus" v-show="!newshow">
          </i>
          <i class="el-icon-remove" v-show="newshow">
        </i> <a>上传算法包</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
</el-row>
  <div v-show="newshow">
   <el-row :gutter="20">
    <el-col :span="12">
     <el-row>
       <el-col :span="12">
         上传算法包
       </el-col>
       <el-col :span="12">
             <el-upload
          class="upload-demo"
          ref="upload"
          action="http://localhost:8081/uploadfile"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          :limit="1"
          :on-exceed="handleExceed"
          :on-change="getname"
          :on-success="clearfile"
          :file-list="fileList"
          :auto-upload="false">
          <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
       </el-col>
       <br/>
        <br/> <br/> <br/> <br/> <br/>
      <el-col :span="12">
         算法包名称
       </el-col>
       <el-col :span="12">

       <el-input
        placeholder="请填入算法包名称"
        v-model="filename"
        clearable>
        </el-input>

       </el-col>
     </el-row>
    </el-col>
    <el-col :span="12">
         <div class="form-group">
                  <label for="username" class="control-label"></label>
                  <div>
                    <p align="left" style="color:gray">提示：
                    </p>
                    <p align="left" style="color:gray">算法包格式： 算法包只支持jar包
                    </p>
                    <p align="left" style="color:gray">输入：输入字段为整个文件或表的全字段
                    </p>
                    <p align="left" style="color:gray">输出：输出字段也为整个文件或表的全字段
                    </p>
                    <p align="left" style="color:gray">清洗标识：输出数据文件或表中增加一列标识
                    </p>
                    <p align="left" style="color:gray">flag:1为清洗过，0为未清洗<br/>
                    </p>

                  </div>
                </div>
    </el-col>
         <el-button @click="submitjar" class="btn btn-success" >提交</el-button>
          <el-button @click='clear' class="btn btn-warning">重置</el-button>
         <el-button type="info">收起</el-button>
   </el-row>
  </div>
</div>
</template>
<script>

  export default {
    data() {
      return {
       newshow:false,
      filename: '', //导入文件名
      jarname:'',
      fileList:[],
      }
    },
    computed: {
   
  },
    created: function () {
      var vm = this;
    

     


    }, 
    methods: {
      changenewshow(){
      var vm = this;
      vm.newshow = !vm.newshow;

       },
       clearfile(){
         this.filename='',
        this.$refs.upload.clearFiles();
       },
       clear: function() {
        var vm = this;
        vm.filename = '';
       this.$refs.upload.clearFiles();
      },
       handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制一次最多只能上传一个算法包文件`);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${ file.name }？`);
      },
      getname:function(file,fileList){
        var vm=this;
         vm.filename=file.name;
         vm.jarname=file.name;
      },
       upload: function(id) {
        var vm = this;
        if (vm.filename=='' || vm.jarname == '') {
          alert("请填写完整信息");
          return;
        }
      this.$refs.upload.submit();
      },
      submitjar(){
           var vm = this;
        var url = "http://localhost:8081/jarfile/addone";
  
           $.ajax({
          url: url,
          type: "POST",
          async:false,
          dataType: 'json',
          data: {
           "jarfilename":vm.jarname,
           "jarname":vm.filename,
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
              if(data.code==0){
            vm.upload();
             vm.$notify({
             title: '成功',
             message: '上传成功',
             type: 'success'
             });}
            else{
              vm.$notify.error({
               title: '错误',
              message: data.msg
            });
            }
            vm.clear();
            vm.fresh();
          },
           error: function (data) {
            vm.$notify.error({
               title: '错误',
              message: data.msg
            })
            console.log(data);
          }
        });
      },
       fresh() {
      this.$emit('fresh'); //主动触发upup方法，'hehe'为向父组件传递的数据
    },
    }
  }
</script>



