<style scoped>


  a {
    color: #000000;
  }
  .el-menu {
    border-radius: 2px;
    list-style: none;
    position: relative;
    margin: 0;
    padding-left: 0;
    background-color: #ffffff;
  }


</style>
<template>
  <el-row class="tac" >
    <el-col :span="50" style="width:200px">
      <el-menu
        default-active="1"
        background-color="#ffffff"
        @select="select"
        :default-active="currentRoute"
        class="el-menu-vertical-demo">
        <el-menu-item index="/home"> <i class="el-icon-menu"></i>流数据清洗工具</el-menu-item>
        <el-menu-item index="/datamanage" >数据源管理</el-menu-item>
        <el-menu-item index="/algmanage" >算法库管理</el-menu-item>
        <el-menu-item index="/jobconfig" >任务配置</el-menu-item>
        <el-menu-item index="/jobmanage" >任务调度</el-menu-item>
        <el-menu-item index="/result" >结果查看</el-menu-item>
      </el-menu>
    </el-col>
  </el-row>


</template>
<script>

  export default {
    data() {
      return {
        currentRoute: '',
        loginflag: '',
      }
    },
    watch: {
      '$route': {
        immediate: true,  // 必须，解决路由同步加载组件时，$watch首次不执行的问题
        handler (to) {
          if (to.matched[0]) {
            this.currentRoute = to.matched[0].path
          }
        }
      },
    },
    created: function () {
      var vm = this;
     // vm.userid = Cookies.get("userid");
  //    vm.userid=window.localStorage.getItem('userid');

     


    }, methods: {
       
      select(key, keyPat) {
        key && this.$router.push(key)
      }
    }
  }
</script>



