<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.datamanage .el-table th{
  text-align: center
}

</style>
<template>
  <div class="datamanage">
    <el-container>
  <el-header style="background-image: url('./static/img/yunjian22.png'); background-size: cover; background-position: top center;">流数据清洗工具</el-header>
  <el-container>
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)"> <nevigation></nevigation></el-aside>
    <el-main>
          <h3 style="text-align:right">
              <i class="el-icon-info">
              </i>
              数据源管理
            </h3>
      <newdatasource @fresh="getdatasource"></newdatasource>
   <el-row :gutter="10">
    <el-col :span="6"><div class="grid-content" style="text-align:left">
          <i class="el-icon-date" >
        </i> <a>数据源列表</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
  <el-row>
    <el-col :span="24">
        <el-table
    :data="tableData4"
    style="width: 100%"
    max-height="500">
    <el-table-column
      fixed
      label="数据源类型"
      width="150">
    <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.type}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="type" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="数据源名称"
      width="200">
    <template slot-scope="scope">
         <span v-show="!scope.row.modcheck">{{scope.row.name}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="name" placeholder="请输入内容"></el-input>
      </span>
    </template>  
    </el-table-column>
    <el-table-column
      label="数据源描述"
      width="200">
    <template slot-scope="scope">
         <span v-show="!scope.row.modcheck">{{scope.row.desc}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="desc" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="表名"
      width="200">
    <template slot-scope="scope">
         <span v-show="!scope.row.modcheck">{{scope.row.tablename}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="tablename" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="分隔符"
      width="120">
    <template slot-scope="scope">
        <span v-show="!scope.row.modcheck">{{scope.row.comma}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="comma" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="第一列列名"
      width="120">
    <template slot-scope="scope">
        <span v-show="!scope.row.modcheck">{{scope.row.first}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="first" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="数据库名"
      width="200">
    <template slot-scope="scope">
        <span v-show="!scope.row.modcheck">{{scope.row.database}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="database" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteconfirm(scope.$index, scope.row,tableData4)"
          type="text"
          size="small"
          plain
          v-show="!scope.row.modcheck">
          移除
        </el-button>
        <el-button
          @click.native.prevent="modcheckit(scope.$index,  scope.row,tableData4)"
          type="text"
          size="small"
          v-show="!scope.row.modcheck">
          修改
        </el-button>
        <el-button
          @click.native.prevent="modchecksuc(scope.$index, scope.row,tableData4)"
          type="text"
          size="small"
          plain
          v-show="scope.row.modcheck">
          确定
        </el-button>
        <el-button
          @click.native.prevent="unmodcheck(scope.$index,scope.row, tableData4)"
          type="text"
          size="small"
          v-show="scope.row.modcheck">
          返回
        </el-button> 
      </template>
    </el-table-column>
  </el-table>
    </el-col>
  </el-row>
</el-row>
    </el-main>
  </el-container>
</el-container>
  </div>
</template>
<script>
  import nevigation from "./sub/nevigation.vue";
  import newdatasource from "./sub/newdatasource.vue"
  export default {
    components: {
      nevigation,
      newdatasource,
    },
    data() {
      return {
          tableData4: [],
          dataid:"",
          type:"",
          name:"",
          desc:"",
          tablename:"",
          comma:"",
          first:"",
          database:"",
      }
    },
    created: function () {
      var vm = this;
      vm.getdatasource();

    },

    methods: {
     deleteRow(index, rows) {
        rows.splice(index, 1);
      },
     modcheckit(index,row,tableData4){
       var vm=this;
       row.modcheck=true;
       vm.dataid=row.dataid;
       vm.type=row.type;
       vm.name=row.name;
       vm.desc=row.desc;
       vm.tablename=row.tablename;
       vm.comma=row.comma;
       vm.first=row.first;
       vm.database=row.database;
      },
     unmodcheck(index,row,tableData4){
       var vm=this;
       row.modcheck=false;
      },
     modchecksuc(index,row,tableData4){
       var vm =this;
       var url ="http://localhost:8081/datasource/update"
       $.ajax({
         url:url,
         type:"POST",
         dataType:"json",
         data:{
           "dataid":vm.dataid,
           "datatype":vm.type,
           "dataname":vm.name,
           "datadesc":vm.desc,
           "tablename":vm.tablename,
           "coma":vm.comma,
           "firstline":vm.first,
           "databasename":vm.database,
         },
          success:function(data){
            if(data.code == 0){
            vm.$notify({
             title: '成功',
             message: '修改成功',
             type: 'success'
             });
           }
            else{
            vm.$notify.error({
             title: '错误',
             message: data.msg,
             });
            }
            console.log(data);
            vm.getdatasource();
       },
       error:function(data){
          vm.$notify.error({
             title: '错误',
             message: "网络错误",
             });
            console.log(data);
       }
       }
      )
     },
     getdatasource(){
        var vm = this;
        vm.tableData4 = [];
         var url = "http://localhost:8081/datasource";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "GET",
          dataType: 'json',
          data: {
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            
          if (data.code == 0) {
            var Obj = data.data;
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.tableData4.push({
                  "dataid":Obj[i].dataid,
                  "type":Obj[i].datatype,
                  "name":Obj[i].dataname,
                  "desc":Obj[i].datadesc,
                  "tablename":Obj[i].tablename,
                  "comma":Obj[i].coma,
                  "first":Obj[i].firstline,
                  "database":Obj[i].databasename,
                  "modcheck":false
                });
                
              }}
              }
              else{
                console.log(data.msg);
                alert(data.data);
              }
          },
           error: function (data) {
            console.log(data);
          }
        });
      },
        deleteconfirm:function(index,row,tableData4){
            var vm=this;
            var r=confirm("请确认是否删除");
            if (r==true)
            {
              vm.deletedatasource(index,row,tableData4);
            }
            else
            {
              return;
            }
          },
      deletedatasource(index,row,tableData4){
         var vm = this;
         var url = "http://localhost:8081/datasource/delete";
        var data_tmp = {
        "dataid":row.dataid,
        };
       $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
               "dataid":row.dataid,
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            if(data.code==0){
             vm.$notify({
             title: '成功',
             message: "删除成功",
             type:'success'
             });}
            else{
             vm.$notify.error({
             title: '错误',
             message: data.msg,
             });
            }
            vm.getdatasource();
                
      
          },
           error: function (data) {
            vm.$notify.error({
             title: '错误',
             message: "网络错误",
             });
            console.log(data);
          }
        });
      }
    }

  }

</script>



