<style >
.newjob .el-table th{
  text-align: center
}


</style>
<template>
  <div class="newjob">

<el-row>
  <el-col :span="6"><div class="grid-content" @click="changenewshow()" style="text-align:left">
      
          <i class="el-icon-circle-plus" v-show="!newshow">
          </i>
          <i class="el-icon-remove" v-show="newshow">
        </i> <a>新建任务</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
</el-row>
  <div v-show="newshow">
   <el-row>
     <el-row>
       <el-col :span="24">
    <el-steps align-center :active="active" finish-status="success">
  <el-step title="选择数据源" description="选择需要清洗的数据源" ></el-step>
  <el-step title="选择算法" description="选择所使用的清洗算法"></el-step>
  <el-step title="完成配置" description="检查无误后点击提交"></el-step>
</el-steps>
   <div v-show="active===0">
     数据源列表
     <el-row>
       <el-col :span="24">
 <el-table
    :data="Datasoucetable"
    style="width: 100%"
    max-height="200">
    <el-table-column
      fixed
      prop="type"
      label="数据源类型"
      width="150">
    </el-table-column>
    <el-table-column
      prop="name"
      label="数据源名称"
      width="200">
    </el-table-column>
    <el-table-column
      prop="desc"
      label="数据源描述"
      width="200">
    </el-table-column>
    <el-table-column
      prop="tablename"
      label="表名"
      width="200">
    </el-table-column>
    <el-table-column
      prop="comma"
      label="分隔符"
      width="120">
    </el-table-column>
    <el-table-column
      prop="first"
      label="第一列列名"
      width="120">
    </el-table-column>
       <el-table-column
      prop="database"
      label="数据库名"
      width="200">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="selectDatesource(scope.$index,scope.row,Datasoucetable)"
          type="text"
          size="small">
          选择
        </el-button>
      </template>
    </el-table-column>
  </el-table>
       </el-col>
     </el-row>
       </div>
       <div v-show="active===1">
     算法列表
     <el-row>
       <el-col :span="24">
 <el-table
    :data="Algtable"
    style="width: 100%"
    max-height="200">
    <el-table-column
      fixed
      prop="algid"
      label="算法ID"
      width="150">
    </el-table-column>
    <el-table-column
      prop="algtype"
      label="算法类型"
      width="200">
    </el-table-column>
    <el-table-column
      prop="algname"
      label="算法名称"
      width="200">
    </el-table-column>
    <el-table-column
      prop="jarname"
      label="算法包"
      width="200">
    </el-table-column>
    <el-table-column
      prop="algdesc"
      label="算法描述"
      width="120">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="selectalg(scope.$index,scope.row, Algtable)"
          type="text"
          size="small">
          选择
        </el-button>
      </template>
    </el-table-column>
  </el-table>
       </el-col>
     </el-row>
       </div>
       <div v-show="active===2">
          <el-row>
     <el-col :span="6">
       任务名称
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="jobname"
        clearable>
    </el-input>
     </el-col>
     <el-col :span="6">
       任务描述
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="jobdesc"
        clearable>
    </el-input>
     </el-col>
   </el-row>
      <el-row>
     <el-col :span="6">
       数据源类型
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="datatype"
        clearable
        disabled>
    </el-input>
     </el-col>
     <el-col :span="6">
       数据源名称
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="dataname"
        clearable
        disabled>
    </el-input>
     </el-col>
   </el-row>
      <el-row>
     <el-col :span="6">
       算法类型
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="algtype"
        clearable
        disabled>
    </el-input>
     </el-col>
     <el-col :span="6">
       算法名称
     </el-col>
     <el-col :span="6">
       <el-input
        placeholder="请输入内容"
        v-model="algname"
        clearable
        disabled>
    </el-input>
     </el-col>
   </el-row>
        <el-button type="primary" @click="submitjob()">提交</el-button>
         <el-button type="danger" @click="clear()">重置</el-button>
         <el-button type="info">收起</el-button>
       </div>
       <div v-show="active===3">
          <el-alert
           title="配置成功，请前往任务调度页面调度任务或者点击下一步添加新任务"
           type="success">
          </el-alert>
       </div>
       <el-button style="margin-top: 12px;" @click="back" :disabled="active==0" v-show="active!=3">上一步</el-button>
       <el-button style="margin-top: 12px;" @click="next" :disabled="active==2" v-show="active!=3">下一步</el-button>
       <el-button style="margin-top: 12px;" @click="newjob1" v-show="active==3">配置新任务</el-button>
       </el-col>
     </el-row>
      
   </el-row>
  </div>
</div>
</template>
<script>

  export default {
    data() {
      return {
       newshow:false,
       active:0,
       jobname:'',
       jobdesc:'',
       datatype:'',
       dataname:'',
       algtype:'',
       algname:'',
       Datasoucetable:[],
       Algtable:[],
       datafile:'',
       algfile:'',
      }
    },
    computed: {
   
  },
    created: function () {
      var vm = this;
      vm.getDatasouce();
      vm.getAlg();

    }, 
    methods: {
      changenewshow(){
      var vm = this;
      vm.newshow = !vm.newshow;

       },
       next() {
        if (this.active++ > 2) this.active = 0;
        // console.log(this.active)
      },
         back() {
        if (this.active-- <0) this.active = 2;
      },
         newjob1() {
         this.active = 0;
         this.clear();
      },
      selectDatesource(index,row,Datasoucetable){
       var vm=this;
       vm.datatype=row.type;
       vm.dataname=row.name;
       vm.datafile=row.database;
       vm.next();
      },
      selectalg(index,row,Algtable){
        var vm=this;
        vm.algtype=row.algtype;
        vm.algname=row.algname;
        vm.algfile=row.jarname;
        vm.next();
      },
      getDatasouce(){
        var vm = this;
        vm.Datasoucetable = [];
         var url = "http://localhost:8081/datasource";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "GET",
          dataType: 'json',
          data: {
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data.data;
            if(data.code==0){
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.Datasoucetable.push({
                  "type":Obj[i].datatype,
                  "name":Obj[i].dataname,
                  "desc":Obj[i].datadesc,
                  "tablename":Obj[i].tablename,
                  "comma":Obj[i].coma,
                  "first":Obj[i].firstline,
                  "database":Obj[i].databasename
                });
                
              }}}
              else{
                console.log(data);
              }
          },
           error: function (data) {
            console.log(data);
          }
        });
      },
      getAlg(){
        var vm = this;
        vm.Algtable = [];
         var url = "http://localhost:8081/alg";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "GET",
          dataType: 'json',
          data: {
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data.data;
            if(data.code==0){
            if(Obj.length>0){
              for (var i = 0; i < Obj.length; i++) {
                vm.Algtable.push({
                  "algid":Obj[i].algid,
                  "algtype":Obj[i].algtype,
                  "algname":Obj[i].algname,
                  "jarname":Obj[i].jarname,
                  "algdesc":Obj[i].algdesc,
                  
                });
                
              }}}
              else{
                console.log(data);
              }
          },
           error: function (data) {
            console.log(data);
          }
        });    
      },
      submitjob(){
           var vm = this;
        var url = "http://localhost:8081/job/addone";
  
           $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
            "jobname":vm.jobname,
           "jobdesc":vm.jobdesc,
           "datatype":vm.datatype,
           "dataname":vm.dataname,
           "datafile":vm.datafile,
           "algtype":vm.algtype,
           "algname":vm.algname,
           "algfile":vm.algfile,
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            //  vm.clear();
           if(data.code==0){
             vm.$notify({
             title: '成功',
             message: '提交成功',
             type: 'success'
             });}
            else{
              vm.$notify.error({
               title: '错误',
              message: data.msg
            });
            }
             vm.active=3
             vm.clear()
             vm.fresh();
       
          },
           error: function (data) {
            console.log(data);
          }
        });
      },
        fresh() {
      this.$emit('fresh'); //主动触发upup方法，'hehe'为向父组件传递的数据
    },
    clear(){
      var vm=this;
       vm.jobname='';
       vm.jobdesc='';
       vm.datatype='';
       vm.dataname='';
       vm.algtype='';
       vm.algname='';
       vm.datafile='';
       vm.algfile='';
       
    }
    }
  }
</script>



