<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.algmanage .el-table th{
  text-align: center
}


</style>
<template>
  <div class="algmanage">
  <el-container>
  <el-header style="background-image: url('./static/img/yunjian22.png'); background-size: cover; background-position: top center;">流数据清洗工具</el-header>
  <el-container>
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)"  > <nevigation></nevigation></el-aside>
    <el-main>
       <h3 style="text-align:right">
              <i class="el-icon-info">
              </i>
              算法库管理
            </h3>
      <uploadalg @fresh="upadatenewalg"></uploadalg>
      <newalg @fresh="getAlg" ref="profile"></newalg>
   <el-row :gutter="10">
    <el-col :span="6"><div class="grid-content" style="text-align:left">
          <i class="el-icon-date" >
        </i> <a>算法列表</a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
  <el-row>
    <el-col :span="24">
        <el-table
    :data="tableData4"
    style="width: 100%"
    max-height="500">
    <el-table-column
      fixed
      label="算法ID"
      width="150">
     <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.algid}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="algid" placeholder="请输入内容" disabled></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="算法类型"
      width="200">
       <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.algtype}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="algtype" placeholder="请输入内容" ></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="算法名称"
      width="200">
       <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.algname}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="algname" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      label="算法包"
      width="200">
      <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.jarname}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="jarname" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
     <el-table-column
      prop="algclass"
      label="算法的类名"
      width="200">
    <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.algclass}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="algclass" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
     <el-table-column
      prop="parameter"
      label="输入参数"
      width="200">
    <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.parameter}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="parameter" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      prop="algdesc"
      label="算法描述"
      width="120">
    <template slot-scope="scope">
      <span v-show="!scope.row.modcheck">{{scope.row.algdesc}}</span>
      <span v-show="scope.row.modcheck">
        <el-input v-model="algdesc" placeholder="请输入内容"></el-input>
      </span>
    </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="200">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="deleteconfirm(scope.$index, scope.row,tableData4)"
          type="text"
          size="small"
          plain
          v-show="!scope.row.modcheck">
          移除
        </el-button>
        <el-button
          @click.native.prevent="modcheckit(scope.$index, scope.row,tableData4)"
          type="text"
          size="small"
          v-show="!scope.row.modcheck">
          修改
        </el-button>
           <el-button
          @click.native.prevent="modchecksuc(scope.$index, scope.row,tableData4)"
          type="text"
          size="small"
          plain
          v-show="scope.row.modcheck">
          确定
        </el-button>
        <el-button
          @click.native.prevent="unmodcheck(scope.$index,scope.row, tableData4)"
          type="text"
          size="small"
          v-show="scope.row.modcheck">
          返回
        </el-button> 
      </template>
    </el-table-column>
  </el-table>
    </el-col>
  </el-row>
</el-row>
    </el-main>
  </el-container>
</el-container>
  </div>
</template>
<script>
  import nevigation from "./sub/nevigation.vue";
  import newalg from "./sub/newalg.vue";
  import uploadalg from "./sub/uploadalg.vue";
  import uploadAlgo from "./sub/uploadAlgo.vue";
  export default {
    components: {
      nevigation,
      newalg,
      uploadalg,
      uploadAlgo,
    },
    data() {
      return {
         tableData4: [],
         algid:"",
         algtype:"",
         algname:"",
         jarname:"",
         algclass:"",
         parameter:"",
         algdesc:"",
      }
    },
    created: function () {
      var vm = this;
      vm.getAlg();
  
    },

    methods: {
     upadatenewalg(){
     this.$refs.profile.getjar()
     },
     modcheckit(index,row,tableData4){
      var vm=this;
      row.modcheck=true;
      vm.algid=row.algid;
      vm.algtype=row.algtype;
      vm.algname=row.algname;
      vm.jarname=row.jarname;
      vm.algclass=row.algclass;
      vm.parameter=row.parameter;
      vm.algdesc=row.algdesc;
     },
     unmodcheck(index,row,tableData4){
       var vm=this;
       row.modcheck=false;
      },
       modchecksuc(index,row,tableData4){
       var vm =this;
       var url ="http://localhost:8081/alg/update"
       $.ajax({
         url:url,
         type:"POST",
         dataType:"json",
         data:{
            "algid":vm.algid,
            "algtype":vm.algtype,
            "algname":vm.algname,
            "jarname":vm.jarname,
            "algclass":vm.algclass,
            "parameter":vm.parameter,
            "algdesc":vm.algdesc
         },
          success:function(data){
            if(data.code == 0){
            vm.$notify({
             title: '成功',
             message: '修改成功',
             type: 'success'
             });
           }
            else{
            vm.$notify.error({
             title: '错误',
             message: data.msg,
             });
            }
            console.log(data);
            vm.getAlg();
       },
       error:function(data){
          vm.$notify.error({
             title: '错误',
             message: "网络错误",
             });
            console.log(data);
       }
       }
      )
     },
     getAlg(){
        var vm = this;
        vm.tableData4 = [];
         var url = "http://localhost:8081/alg";
        var data_tmp = {
        };
       $.ajax({
          url: url,
          type: "GET",
          dataType: 'json',
          data: {
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj=data.data;
           if(data.code==0){
            if(data.data.length>0){
              for (var i = 0; i < data.data.length; i++) {
                vm.tableData4.push({
                  "algid":Obj[i].algid,
                  "algtype":Obj[i].algtype,
                  "algname":Obj[i].algname,
                  "jarname":Obj[i].jarname,
                  "algclass":Obj[i].algclass,
                  "parameter":Obj[i].parameter,
                  "algdesc":Obj[i].algdesc,
                  "modcheck":false,
                  
                });
                
              }}}
              else{
                alert(data.msg);
                console.log(data);
              }
          },
           error: function (data) {
             alert("网络错误");
            console.log(data);
          }
        });
     },
   deleteconfirm:function(index,row,tableData4){
            var vm=this;
            var r=confirm("请确认是否删除");
            if (r==true)
            {
              vm.deletealg(index,row,tableData4);
            }
            else
            {
              return;
            }
          },
      deletealg(index,row,tableData4){
         var vm = this;
         var url = "http://localhost:8081/alg/deleteone";
       $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
         "algid":row.algid,
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            if(data.code==0){
             vm.$notify({
             title: '成功',
             message: "删除成功",
             type:'success'
             });}
            else{
             vm.$notify.error({
             title: '错误',
             message: data.msg,
             });
            }
            vm.getAlg();
                
      
          },
           error: function (data) {
              vm.$notify.error({
             title: '错误',
             message: "网络错误",
             });
            console.log(data);
          }
        });
      }
    }

  }

</script>



