<script>

  export default {
    data() {
      return {
       newshow:false,
       picked: 'txt',
       fileList: [],
       tablenameT:'',
       comma:'',
       firstiscol:'是',
       name:'',
       name_desc:'',
       connectsuc:false,
       connectioning:false,
       servername:'',
       username:'',
       password:'',
       port:'',
       linkname:'',
       database:'',
       tablename:'',
       filename:'',
       filename1:'',
      }
    },
    computed: {
    mysqlshow1: function() {
      var vm = this;
    //  vm.subclear();
      if (vm.picked == "mysql")
        return true;
      else
        return false
    },
    csvshow1: function() {
      var vm = this;
     // vm.subclear();
      if (vm.picked == "csv")
        return true;
      else
        return false
    },
    txtshow1: function() {
      var vm = this;
    //  vm.subclear();
      if (vm.picked == "txt")
        return true;
      else
        return false
    },
    hbaseshow1: function() {
      var vm = this;
     // vm.subclear();
      if (vm.picked == "hbase")
        return true;
      else
        return false
    },
    tablenametable: function() {
      var vm = this;

      return vm.databaseObj[vm.database];
    }

  },
    created: function () {
      var vm = this;
    

     


    }, 
    methods: {
       changenewshow(){
      var vm = this;
      vm.newshow = !vm.newshow;

       },
   handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制一次最多只能上传一个数据文件`);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${ file.name }？`);
      },
      getname:function(file,fileList){
        var vm=this;
         vm.filename1=file.name;
      },

      submitdatasource(){
      var vm = this;
        var url = "http://localhost:8081/datasource/addone";
      if(vm.picked=="csv"){
         var data_tmp = {
           "datatype":"csv",
           "dataname":vm.name,
           "datadesc":vm.name_desc,
           "tablename":vm.tablenameT,
           "comma":",",
           "first":vm.firstiscol,
           "database":vm.filename1
          };
           $.ajax({
          url: url,
          type: "POST",
          dataType: 'json',
          data: {
           "datatype":"csv",
           "dataname":vm.name,
           "datadesc":vm.name_desc,
           "tablename":vm.tablenameT,
           "coma":",",
           "firstline":vm.firstiscol,
           "databasename":vm.filename1
          },
           success: function (data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            if(data.code==0){
            vm.upload();
             vm.$notify({
             title: '成功',
             message: '上传成功',
             type: 'success'
             });
            vm.clear();
            vm.fresh();}
            else{
              vm.$notify.error({
               title: '错误',
              message: data.msg
            });
            }
            console.log(data);
          },
           error: function (data) {
              vm.$notify.error({
               title: '错误',
              message: data.msg
            })
            console.log(data);
          }
        });
      }
      },
       fresh() {
      this.$emit('fresh'); //主动触发upup方法，'hehe'为向父组件传递的数据
    },
    clear(){
      var vm = this;
      vm.name='';
      vm.name_desc='';
      vm.filename='';
      vm.filename1='';
      vm.tablename='';
      vm.tablenameT='';
      vm.comma='';
      vm.database='';
      vm.linkname='';
      this.$refs.upload.clearFiles();
    },
     upload: function() {
      this.$refs.upload.submit();
 
      },
      clearfile:function(){
     this.$refs.upload.clearFiles();
      },
    }
  }
</script>



