<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
  /*a  upload */

  .a-upload {
    padding: 4px 10px;
    height: 30px;
    line-height: 20px;
    position: relative;
    cursor: pointer;
    color: #888;
    background: #fafafa;
    border: 1px solid #ddd;
    border-radius: 4px;
    overflow: hidden;
    display: inline-block;
    *display: inline;
    *zoom: 1
  }

  .a-upload input {
    position: absolute;
    font-size: 100px;
    right: 0;
    top: 0;
    opacity: 0;
    filter: alpha(opacity=0);
    cursor: pointer
  }

  .a-upload:hover {
    color: #444;
    background: #eee;
    border-color: #ccc;
    text-decoration: none
  }

</style>
<template>
  <div class="uploadAlgo">
    <!--导入算法-->
    <div class="row">
      <div class="col-md-12" @click='changeNewshow'>
      <span class="pull-left">
        <i class="fa fa-plus" v-show="!newshow">
        </i>
        <i class="fa fa-minus" v-show="newshow">
        </i>
        上传算法包
      </span>
        <hr>
      </div>
      <div class="col-md-12" v-show="newshow">
        <div class="row">
          <div class="col-md-12">
            <div class="row">
              <div class="col-md-6">
                <div class="row">
                  <div class="col-md-12">

                    <div class="form-group">

                      <label for="alg_jar" class="col-sm-4 control-label">上传算法包</label>
                      <div class="col-sm-8" @change='changename'>
                        <span class="pull-left btn btn-warning">
                            <input type="file"  class="pull-left btn btn-warning" id="alg_jar" name="alg_jar" accept=".jar"  >点击上传文件
                        </span><br/>{{filename}}

                      </div>
                    </div>


                  </div>
                  <div class="col-md-12">
                    <br/>
                    <br/>
                    <br/>

                    <div class="form-group">
                      <label for="src_val" class="col-sm-4 control-label">算法包名称<br/>（勿含中文）</label>
                      <div class="col-sm-8">
                        <input type="text" class="form-control" v-model="filename" placeholder="请填入算法包名称" :disabled="a">
                      </div>
                    </div>
                  </div>
                </div>

              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label for="username" class="col-sm-2 control-label"></label>
                  <div class="col-sm-10">
                    <p align="left" style="color:gray">提示：
                    </p>
                    <p align="left" style="color:gray">算法包格式： 算法包只支持jar包
                    </p>
                    <p align="left" style="color:gray">输入：输入字段为整个文件或表的全字段
                    </p>
                    <p align="left" style="color:gray">输出：输出字段也为整个文件或表的全字段
                    </p>
                    <p align="left" style="color:gray">清洗标识：输出数据文件或表中增加一列标识
                    </p>
                    <p align="left" style="color:gray">flag:1为清洗过，0为未清洗<br/>
                    </p>


                  </div>
                </div>
              </div>

              <div class="col-md-12" >
                <div class="form-group">
                  <br/>
                  <el-button @click="upload" class="btn btn-success" :disabled="b" v-show="!connectioning">
                    提交
                  </el-button>
                  <el-button type="warning" class="btn btn-success" :loading="true" v-show="connectioning">提交中</el-button>
                  <el-button @click='clear' class="btn btn-warning">
                    重置
                  </el-button>
                  <el-button @click='changeNewshow' class="btn btn-info">收起</el-button>
                </div>
              </div>
            </div>

            <br/>
            <br/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import $ from 'jquery';
  export default {
    data() {
      return {
        connectioning:false,
        a:false,
        b:false,
        url: '',
        userid: '',
        newshow: false,
        filename: '' //导入文件名
      }
    },
    created: function() {
      var vm = this;
 //     vm.userid = Cookies.get("userid");
    //  vm.userid=window.localStorage.getItem('userid');
    // vm.getmenu();
        //  vm.getcurrent();
      var ip = '';
      var port = '';
      var pub = '';

    },
    methods: {
      getmenu:function () {
        var vm=this;
        var data_tmp = {
          module:''
        };
        var url = "/bigdatacleaner/api/isLogin";
        //    var url = vm.url + "api/login";

        $.ajax({
          url: url,
          async:false,
          type: "POST",
          dataType: 'json',
          data: {
            'data_json':JSON.stringify(data_tmp)
          },
          success: function(data) {
            //将data json字符串转换为json对象
            var Obj = data;
            // console.log(Obj);
            var redirect = Obj.info.redirect;
            var cause = Obj.info.cause;
            var modules = Obj.info.modules;
            if (Obj.status == 0) {
            }
            //   console.log("valid:"+valid);
            else {
              if (redirect == 1) {
                if(cause==1) {
                  //    alert("用户未登录或已超时,无法访问页面!")
                  window.location.href = "#/login";
                  return;
                }
                else if(cause == 2){
                  //    alert("该账号在其他地方登录,您被迫下线!")
                  window.location.href = "#/login";
                  return;
                }
                else {
                  //     alert("该账号长期未操作,会话终止,请重新登录!")
                  window.location.href = "#/login";
                  return;
                }
              }

              else if (redirect ==''&& cause==''){
                //  //alert("Error")
                window.location.href = "#/login";
                return;
              }
              else{
                //  alert("查看是否登录失败")
                return;
              }
            }

            //  window.location.reload();
          },
          error: function(data) {
            //alert("Error")
            console.log(data);
          }
        });
      },
       getcurrent:function(){
         var vm=this;
        var url = "/bigdatacleaner/api/currentuser";
        $.ajax({
          url: url,
          async:false,
          type: "POST",
          dataType: 'json',
          data: {
          },
          success: function(data) {
            //将data json字符串转换为json对象
            var Obj = data;
            // console.log(Obj);
            var userid = Obj.info.id;
            var username = Obj.info.username;
            if (Obj.status == 0) {
               vm.userid = userid;
          //     vm.username = username;
              }
            else {
          //    //alert("用户获取失败")
            }
          },
          error: function(data) {
            console.log(data);
          }
        });
      },
      changeNewshow: function() {
        var vm = this;
        vm.newshow = !vm.newshow;
      },
      clear: function() {
        var vm = this;
        vm.filename = '';
        vm.a=false;
        var obj = document.getElementById('alg_jar');
        obj.outerHTML = obj.outerHTML;

      },
      fresh() {
        this.$emit('fresh'); //主动触发upup方法，'hehe'为向父组件传递的数据
      },
      changename: function() {
        var vm = this;
        vm.a =true;
        var path = $("#alg_jar").val();
        var pathstr = path.split("\\");
        vm.filename = pathstr[pathstr.length - 1];
      },
      upload: function(id) {
        var vm = this;
//        var url = vm.url + "api/algorithms/upload/";
        var url = "http://localhost:8080/dataclean/servlet/DataCleanServlet";
        var formData = new FormData();
        formData.append("jar", document.getElementById("alg_jar").files[0]);

        var path = $("#alg_jar").val();
        var pathstr = path.split("\\");
        var filetypes =[".jar"];
        var fileend = vm.filename.substring(vm.filename.indexOf("."));
        var isnext = false;
        var filenoend = vm.filename.substring(0,vm.filename.indexOf("."))
        vm.a=true;
        if (path == '') {
          alert("请选择文件");
          return;
        }
        console.log("filename:" + vm.filename);
        if (vm.filename == '') {
          alert("请填写完整信息");
          return;
        }

        var testjar=pathstr[pathstr.length-1].split('.');
        // if(testjar[testjar.length-1]!="jar"){
        //   alert("请选择jar格式的文件");
        //   return;
        // }
        if (/^[\u4e00-\u9fa5]+$/i.test(filenoend)) {

          alert("算法包名称请不要使用中文");
          return;
        }
        vm.connectioning = true;
        vm.b=true;
        formData.append("name", vm.filename);
        formData.append("userid", vm.userid);
        //   console.log("path"+path);
        //              $.ajax({
        //                  url: url,
        //                  type: 'POST',
        //                  cache: false,
        //                  data: formData,
        //                  processData: false,
        //                  contentType: false,
        //                  success: function(data) {
        //
        //                  },
        //                  error: function(data) {}
        //              });
        //

        $.ajax({
          url: url,
          type: 'POST',
          data: formData,
          /**
           * 必须false才会避开jQuery对 formdata 的默认处理
           * XMLHttpRequest会对 formdata 进行正确的处理
           */
          processData: false,
          /**
           *必须false才会自动加上正确的Content-Type
           */
          contentType: false,
          dataType: 'json',

          success: function(data) {
            //将data json字符串转换为json对象
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            //    console.log(Obj);

            if (Obj.status == 0) {
              // console.log(data);
              alert("上传成功");
              vm.clear();
            } else {
              alert("上传失败" + ":" + Obj.info);

            }
            vm.a=false;
            vm.clear();
            vm.fresh();
            vm.newshow = false;
            vm.connectioning = false;
            vm.b=false;
          },
          error: function(data) {
            alert("上传出错");
            console.log(data); //将json对象转成json字符串。
            vm.clear();
            vm.fresh();
            vm.connectioning = false;
            vm.b=false;
          }
        });
      }
    }
  }

</script>




