<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(3) {
    /* background-color: #99a9bf; */
    background-image:url(/static/img/datamanage.png);
    background-size: cover
  }
  
  .el-carousel__item:nth-child(4) {
    background-image:url(/static/img/algmanage.png);
    background-size: cover
  }
   .el-carousel__item:nth-child(5) {
    background-image:url(/static/img/jobconfig.png);
    background-size: cover
  }
  .el-carousel__item:nth-child(6) {
    background-image:url(/static/img/jobmanage.png);
    background-size: cover
  }
    .el-carousel__item:nth-child(7) {
    background-image:url(/static/img/result.png);
    background-size: cover
  }
</style>
<template>
  <div class="home">
  
    <el-container>
  <el-header style="background-image: url('./static/img/yunjian22.png'); background-size: cover; background-position: top center;">流数据清洗工具</el-header>
  <el-container>
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)"> <nevigation></nevigation></el-aside>
    <el-main>
      <el-row>
        <el-col :span="24">
          <h1>欢迎使用流数据清洗工具</h1>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-carousel :interval="4000" type="card" height="400px">
            <el-carousel-item v-for="item in data" :key="item" >
              <h3 @click="turnto(item)">{{ item }}</h3>
            </el-carousel-item>
          </el-carousel>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</el-container>
   <!-- <el-row >
  <el-col :span="4"><div class="grid-content">
    <nevigation></nevigation></div></el-col>
  <el-col :span="20"><div class="grid-content ">
    </div></el-col>
</el-row> -->
  </div>
</template>
<script>
  import nevigation from "./sub/nevigation.vue";

  export default {
    components: {
      nevigation,
      
    },
    data() {
      return {
        data:["数据源管理","算法库管理","任务配置","任务调度","结果查看"]
      }
    },
    created: function () {
      var vm = this;


    },

    methods: {
     turnto(item){
       var vm=this;
       if(item=="数据源管理"){
         this.$router.push('/datamanage');
       }
       else if(item=="算法库管理"){
         this.$router.push('/algmanage');
       }
         else if(item=="任务配置"){
         this.$router.push('/jobconfig');
       }
        else if(item=="任务调度"){
         this.$router.push('/jobmanage');
       }
       else{
           this.$router.push('/result');
       }
     },

    }

  }

</script>



